<template>
  <nav class="navbar">
    <div class="navi" :class="{active: naviActive}" @click="[toggleBtn(), sideMenu()]"><i class="fa-solid fa-burger burger"></i></div>
  </nav>
  <div class="sideMenu bg-o-black">
    <div class="sideMenu_click" @click="[naviActive = false, sideMenu()]"></div>
    <div class="sideMenu_box">
      <router-link to="/" @click="[toggleBtn(), sideMenu()]" class="sideList">Home</router-link>
      <router-link to="/about" @click="[toggleBtn(), sideMenu()]" class="sideList">About</router-link>
    </div>
  </div>
  <router-view/>
</template>
<script>
export default {
  data () {
    return {
      naviActive: false
    }
  },
  methods: {
    toggleBtn () {
      if (this.naviActive === false) {
        this.naviActive = true
      } else {
        this.naviActive = false
      }
    },
    sideMenu () {
      const navi = document.querySelector('.navi')
      if (navi.className === 'navi active') {
        setTimeout(() => {
          document.querySelector('.sideMenu').classList.remove('active')
        }, 500)
        document.querySelector('.sideMenu_box').classList.remove('active')
      } else {
        document.querySelector('.sideMenu').classList.add('active')
        setTimeout(() => {
          document.querySelector('.sideMenu_box').classList.add('active')
        }, 100)
      }
    }
  }
}
</script>
<style>
  @import url(../css/common.css);
</style>
